body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.bifoldContainer{
  display: -webkit-inline-flex;
  display: inline-flex;
  padding: 25px;
  
}
.resume{
  width: 70%;
  border-right: 4px  double black;
  padding-right: 20px;
  -webkit-justify-content: center;
          justify-content: center;
}
.skills{
  margin-left: 30px;

}
.project{
  background-color: slategray;
  border-radius: 30px;
  border: 3px solid;
  padding: 20px;
  margin-top: 20px;
  
}
img{
  width: 300px;
  height: auto;
}

.App-header{
    min-height: 50% !important;
    height: 400px;
    background-image: url('https://cdn.pixabay.com/photo/2017/01/15/09/53/texture-1981129__340.jpg');
    color: white;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 200px;
}
h1.title{
    font-size: 50px;
}
hr{
    width: 40%;
}
